


































	
	
	export default {
		data() {
			return {
				title: 'Hello',
				notice:[],
				banners:[]
			}
		},
		computed:{
			applist(){
				return this.$store.getters.getGameInfoList;
			}
		},
		onLoad() {
			this.$utils.tRequest({
				url: this.$webUrl+'home_all',
				method: 'GET',
				data: {
					 pagesize:10,
					 page:1
				},
				success: res => {
					if(res.code == 0){
					    this.$store.dispatch("setGameInfoList",res.data.applist);
						this.banners = res.data.banner;
						this.notice = res.data.notice;
					}else{ 
						uni.showToast({
							title: res.msg,
							mask: false,
							duration: 1500,
							icon:'none'
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		onNavigationBarButtonTap:function(e){
			uni.navigateTo({
				url: '../ray/notify/notify',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		methods: {
			nodata:function(e){
				var value = e.currentTarget.dataset.callindex;
				console.log(value)
				if(value == ""){
					uni.showModal({
						title: '提示',
						content: '开发中。。敬请期待',
						showCancel: false,
						cancelText: '',
						confirmText: '知道了',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
				}
				
			},
           toplay:function(e){
			   var index = e.currentTarget.dataset.index;
			   uni.navigateTo({
			   	url: '../ray/rayGameList/rayGameList?gameIndex='+index,
			   	success: res => {},
			   	fail: () => {},
			   	complete: () => {}
			   });
			   
		   },
		   gameinfo:function(e){
			   var index = e.currentTarget.dataset.index;
			   console.log("index = "+index);
			   uni.navigateTo({
			   	url: '../ray/gameInfo/gameInfo?gameIndex='+index,
			   	success: res => {},
			   	fail: () => {},
			   	complete: () => {}
			   });
		   },
		   click:function(){
			  
		   }
		}
	}
